dialog.modal-dialog {
  transition: all 0.2s;
  opacity: 0;
  transform: scale(0.9);
  z-index: 3000;
  border: none;
  padding: 0;
  margin: auto;
  &[open] {
    opacity: 1;
    transform: translateX(0);
  }
  &::backdrop {
    background-color: rgba(0, 0, 0, 0.7);
  }
  &.with-layout {
    border-radius: 8px;
    min-width: 360px;
    background-color: var(--bg);
    display: flex;
    flex-direction: column;
  }
}
