header.header {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding-top: 10px;
  padding-bottom: 10px;
  padding-right: 20px;
  > .tag-line {
    display: flex;
    align-items: center;
    color: var(--color-grey);
    > h4 {
      font-size: 1.2rem;
      margin-block: 0;
      font-weight: normal;
    }
    .tag-line-decoration {
      height: 1px;
      width: 3rem;
      background-color: var(--color-grey);
      &.left {
        margin-right: 1rem;
      }
      &.right {
        margin-left: 1rem;
      }
    }
  }
  > div.prompt-and-buttons-container {
    display: flex;
    align-items: center;
    width: 100%;
    gap: 10px;
  }
}
