div.prompt {
  display: flex;
  align-items: center;
  width: 100%;
  gap: 10px;
  > textarea {
    margin-block: 0;
    margin: 1rem auto;
    color: var(--fg);
    flex: 1;
    text-align: center;
    border: none;
    font-size: var(--font-size-xlarge);
    font-weight: bold;
    resize: none;
    &:focus {
      outline: none;
    }
    &.hidden-textarea {
      opacity: 0;
      position: absolute;
      top: -100vh;
    }
  }
}
