.game-piece {
  height: 40px;
  width: 40px;
  background-color: black;
  border-radius: 50%;
  display: inline-block;
  background-size: contain;
  background-position: center;
  background-repeat: no-repeat;
  cursor: grab;
  &.absolute {
    position: absolute;
    transform: translateX(-50%) translateY(-50%);
  }
}
