.timer {
  border: solid 2px var(--fg);
  border-radius: 12px;
  padding: 16px 20px;
  box-sizing: border-box;
  font-size: var(--font-size-medium);
  height: 48px;
  font-weight: bold;
  opacity: 1;
  transition: opacity 0.8s ease-in-out;
  display: flex;
  gap: 10px;
  &.exiting {
    opacity: 0;
  }
  > span.timer-time {
    user-select: none;
  }
  > button {
    color: var(--color-primary-1);
    border: none;
    background: none;
    cursor: pointer;
    padding: 0;
    &:focus-visible {
      outline: none;
    }
  }
}
