.team-member {
  display: flex;
  flex-direction: column;
  align-items: center;
  border-radius: 10px;
  padding: 8px;
  box-sizing: border-box;
  font-size: 14px;
  width: 80px;
  box-shadow: rgba(149, 157, 165, 0.2) 0px 8px 24px;
  > div.game-piece-container {
    position: relative;
  }
  > span {
    text-transform: uppercase;
    color: var(--color-grey);
    user-select: none;
  }
}
