div.timer-setting {
  display: flex;
  width: 100%;
  justify-content: center;
  align-items: center;
  gap: 10px;
  input.timer-setting-input {
    width: 20px;
    padding: 16px 20px;
    border-radius: 12px;
    border: 2px solid var(--fg);
    font-weight: bold;
    text-align: center;
    &:focus {
      outline: none;
      border-color: var(--color-primary-5);
    }
  }
}
